import * as React from "react";
import { SoundPlayer } from './SoundPlayer';
import './Grid.css';

const el = React.createElement

interface SquareState {
    size: string;
    pressed: boolean;
    color: string;
    borderColor: string;
}

class Square extends React.Component<{}, SquareState> {
    constructor(props: {}) {
        super(props);
        this.state = {
            size: '4',
            pressed: false,
            color: '#fff',
            borderColor: '#fff',
        };
    }

    pulse(time: number) {
        this.setState({ pressed: false, borderColor: '#fff'});
        // sleep for time seconds
        this.setState({ pressed: true, borderColor: this.state.color});
    }

    // Set random color if not already pressed
    updateSquare() {
        if (!this.state.pressed) {
            this.setState({ color: '#' + Math.random().toString(16).substr(-6) })
        }
    }

    squarePressed() {
        if (!this.state.pressed)
            this.setState({ pressed: true, borderColor: this.state.color});
        else
            this.setState({ pressed: false, borderColor: '#fff'});
    }

    render() {
        return el(
            'div',
            { 
                onClick: () => this.squarePressed(), 
                onMouseEnter: () => this.updateSquare(),
                style: {
                    backgroundColor: this.state.color,
                    border: '6px solid' + this.state.borderColor,
                    height: this.state.size+'em',
                    width: this.state.size+'em',
                    transition: '0.5s' } 
            },
            null
        );
    }
}

interface GridState {
    width: number;
    height: number;
    midiPlayer: any;
}

// TODO: Kr onMouseBlabla-event fr alla Square, men gr det i Grid-klassen istllet s att man kan ndra dom i nrheten? Vnta lite med detta, kanske r lite fr krvande.. 
// TODO: Spela melodi allt eftersom man eller rutor? Klickar man p en spelas den noten hgre och den rutan pulserar samtidigt som noten spelas (vnta lite med det) etc. 
//       Lgg till alla rutor i en array som man klickat p, upprepa s mnga noter ifrn lten och lt de man klickat p pulsera.
//       Istllet fr en loop i en funktion i SoundPlayer-klassen s har jag currentNote s den vet vilken som ska spela hr nst?
export class Grid extends React.Component<{}, GridState> {
    private squares: Array<any>;
    private tableCells: Array<any>;
    
    constructor(props: {}) {
        super(props);
        this.state = {
            width: 0,
            height: 0,
            midiPlayer: new SoundPlayer({})
        };

        this.squares = [];
        this.tableCells = [];
        this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    }

    fillGrid() {
        if(!this.tableCells.length) {
            // TODO: fixa s att det blir rtt storlek
            for (let i = 0; i < this.state.height / 80; i++){
                let tableRow = [];
                let squaresRow = [];

                for (let j = 0; j < this.state.width / 80; j++){
                    //squaresRow.push(el(Square, {posx: i, posy: j}));
                    squaresRow.push(el(Square, {}));

                    tableRow.push(el('td', { 
                        key: i * Math.ceil(this.state.width / 40) + j,
                        style: { padding: '-100px' },
                    }, squaresRow[squaresRow.length -1]));
                }

                this.squares[i] = squaresRow;
                this.tableCells[i] = el('tr', { key: i * -1 }, tableRow)
            }
        }
    }

    componentDidMount() {
        this.updateWindowDimensions();
        window.addEventListener('resize', this.updateWindowDimensions);
    }
      
    componentWillUnmount() {
        window.removeEventListener('resize', this.updateWindowDimensions);
    }
      
    updateWindowDimensions() {
        this.setState({ width: window.innerWidth, height: window.innerHeight });
    }

    render() {
        this.fillGrid(); // TODO: Fixa s fillGrid() endast krs en enda gng.

        return (
            <div>
                <table className='grid' cellSpacing='0' cellPadding='0'>
                    <tbody>
                        {this.tableCells}
                    </tbody>
                </table>

                <div>
                    <SoundPlayer></SoundPlayer>
                </div>
            </div>
        )
    }
}